import { writable } from "svelte/store";
class Popups {
    constructor() {
        this.popups = {};
        this.id = 0;
    }
    remove(id) {
        delete this.popups[id];
        popups.update((v) => v);
    }
    add(message, delay = 2000, color = "bg-white") {
        this.popups[this.id] = { message, delay, color };
        this.id += 1;
        popups.update((v) => v);
    }
}
const popups = writable(new Popups());
export { Popups, popups };
